
html{
  width: 100%;
  height: 100%;
}
body {
  margin: 0;
  font-family: 'Josefin Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  background-color: rgb(197, 194, 194);
}
.app{
  width: 100%;
  height: 100%;
}
#root{
  width: 100%;
  height: 100%;
}
.container{
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
   
}
a{
  text-decoration: none;
}
a:hover{
  text-decoration: none;
  color: #b7410e;
}
.fas{
  cursor: pointer;
}

/* Botões */
.botao{
  background-color:#b7410e;
  border: 1px solid black;
  color: rgb(19, 13, 13);
  font-weight: bolder;
  border-radius: 0.25em;
  height: 3em;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1em;
  width: 15em;
  margin-left: 0;
  text-decoration: none;
  transition: .45s;
  
}
.botao:hover{
  background-color:#f8a480;
  color: rgb(19, 13, 13);
  border: 1px solid black;
  text-decoration: none;
}
/* PAINEL */

/* Painel Editar */
.painel-editar{
  margin-top: 5em;
}
.painel-editar h1{
  font-size: 4em;
  text-align: center;
  margin-bottom: 0.8em;
  font-weight: bolder;
}
.painel-editar label{
  font-size: 1.5em;
  font-weight: bolder;
}
.painel-editar input{
  width: 51em;
}
.campoeditar{
  display: flex;
  flex-wrap: wrap;
}
.campoeditar label{
  padding-right: 15em;
}
.painel-busca{
  display: flex;
  flex-wrap: wrap;
}

.painel-editar-escolha a{
  width: 25em;
}


/* Painel Menu */
.painelmenu{
  margin-top: 5em;
}
.painelmenu h1{
  font-size: 4em;
  text-align: center;
  margin-bottom: 0.8em;
  font-weight: bolder;
}
.botaomenu{
  width: 25em;
}

/* Painel Cadastrar Filmes */
.painel-cadastrar-container h1{
  margin-top: 1.3em;
  font-size: 3em;
  font-weight: bolder;
  text-align: center;
}
.painel-cadastrar{
  display: flex;
  flex-wrap: wrap;
}
.painel-cadastrar label{
  font-size: 1.3em;
  font-weight: bolder;
}
.painel-cadastrar a{
  width: 25em;
}

/* Painel Cadastro Admin */

.painel-cadastro-admin h1{
  margin-top: 1.3em;
  font-size: 3em;
  font-weight: bolder;
  text-align: center;
}
.painel-cadastro-admin label{
  font-size: 1.3em;
  font-weight: bolder;
}
.botao-admin{
  width: 25em;
}
.botoes-admin{
  display: flex;
}


/* Painel Remover Filmes*/
.painel-remover-form{
  
  display: flex;
  flex-wrap: wrap;
}
.painel-remover-form input{
  width: 51em;
}


.painel-remover h1{
  font-size: 4em;
  text-align: center;
  margin-bottom: 0.8em;
  font-weight: bolder;
  margin-top: 1em;
}
.painel-remover-form label{
  font-size: 1.5em;
  font-weight: bolder;
  padding-right: 15em;
}
.painel-remover-form a{
  width: 25em;
}

/* Painel Login */
.painel-login {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

}
.painel-login h1{
  font-size: 3em;
  text-align: center;
  margin-bottom: 0.8em;
  font-weight: bolder;
  margin-top: 1.5em;
}
.painel-login label{
  font-size: 1.5em;
  font-weight: bolder;
  padding-right: 15em;
}

.painel-login input{
  margin-top: 0.2em;
  margin-bottom: 0.8em;
}

.botoes-login{
  display: flex;
}
.botao-painel-login{
  width: 25em;
}


/* Botões Painel */
.btn-painel-editar{
  margin: 1em;
  width: 25em;
  margin-left: 0;
  text-decoration: none;
  transition: .45s;
}

/* Header */
.nav-link:hover{
  color: #f8a480;
}
/* Catalogo Filmes */

.text-titulo{
  font-weight: bolder;
}



/* Categorias */
.tags{
  display: flex;
  align-items: center;
  margin-top: 1em;
  margin-bottom: 1em;
}
.tags button{
  width: 11.3em;
}

.tags-text{
  font-weight: bold;
  margin-right: 0.5em;
  font-size: 2rem;
}
.botao-categoria{
  width: 25em;
  
}
.filmes-categoria{
  margin: 1em;
  display: flex;
  flex-direction: column;
}
.filmes-categoria img{
  
  width: 15em;
  height: 15em;
}
.filmes-categoria span{
  font-weight: bolder;
}
.categoria-filmes{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.selectcategorias{
  width: 80em;
}
#campoeditarcategoria{
  display: none;
}
#campoeditarcategoria select{
  width: 51em;
}


/* Comentarios */

.lista-comentarios{
  width: 30em;
  border: .5px dotted black;
  background-color: rgb(228, 224, 224);
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  
}
.lista-comentarios span{
  font-weight: bolder;
}
.comentar{
  width: 15em;
}

/* Home */

.lançametos{
  display: flex;
  flex-wrap: wrap;
  width: 15em;
  margin-left: 1.2em;
  margin-right: 0;
  margin-bottom: 1em;
}
.home{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.home h1{
  margin-top:.5em;
  font-size: 4em;
  font-weight: bolder;
}
.home h2{
  font-size: 3em;
 
}
.home h3:hover{
  color: #b7410e;
}
.secao-lancamentos{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.lançametos img{
  width: 15em;
  height: 15em;
}
.lançametos a{
  text-decoration: none;
  color: rgb(19, 13, 13);
}
.lançametos h3{
  font-size: 1.5em;
}
.botao-home{
  margin-top:.1em;
  width: 35em;
  
}
